@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,700;1,100;1,200;1,500&display=swap);
nav{width:100vw;height:auto;background:var(--default-color);position:fixed;z-index:999}.nav-center{display:flex;flex-direction:row;justify-content:space-around;padding:1rem}.logo{font-size:1.5rem;font-weight:300;background:var(--linear-gradient);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.nav-toggle{font-size:1.5rem;margin:0 1rem;color:var(--secondary-color);background:transparent;border-color:transparent;transition:var(--transition);cursor:pointer}.nav-toggle:hover{filter:var(--filter);transform:rotate(90deg)}.button-mode{font-size:1.5rem;color:var(--lighting-color);background:transparent;border-color:transparent;transition:var(--transition);cursor:pointer}.links{list-style:none;display:flex}.links a{color:var(--primary-color);font-size:1.2rem;text-transform:capitalize;text-decoration:none;letter-spacing:.1rem;display:block;padding:.5rem 1rem;transition:var(--transition)}.links a:hover{filter:var(--filter);padding-left:1.5rem}.links-container{overflow:hidden;transition:var(--transition)}.show-container{height:10rem}
.main-container{width:100vw;height:100vh;background:url(/static/media/background-ilustration.31d24c36.svg) 100% no-repeat}.main-container .main-content{padding:2rem;text-align:center;justify-content:space-around;align-items:center}.main-container .main-content h1{font-size:5rem;display:flex;justify-content:center;align-items:center;text-align:center;margin-top:17rem;background:var(--linear-gradient);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.main-container .main-content p{font-size:1rem;margin:.2rem;color:var(--primary-color)}
.about{width:100vw;height:100vh;display:flex;justify-content:space-around}.about .about-content{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.about .about-img{width:30vw;height:80vh}.about .about-description{width:50vw;height:80vh;margin:2rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.about .about-description h1{text-align:center;margin-top:2rem;background:var(--linear-gradient);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.about .about-description p{text-align:center}
.cases{width:100vw;height:50vw;display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:5rem}.cases h1{font-size:5rem;background:var(--linear-gradient);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.cases .carousel-wrapper{width:60rem;height:30rem;margin:4rem 0;display:flex;justify-content:center;align-items:center}.cases .carousel{width:100%;height:90%;margin-top:2rem;padding:2rem}.cases .rec.rec-arrow{visibility:hidden}
img{width:100%;height:100%}
.scan{width:100vw;height:100vh;padding:4rem;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center}.scan h2{font-size:5rem;background:var(--linear-gradient);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.scan .scan-subtitle{font-size:1rem;color:var(--primary-color)}.scan img{margin-top:1em;width:10em;height:10em}
footer{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;margin-top:1rem;padding:1rem;font-size:.8rem;color:var(--primary-color)}footer ul{display:flex;justify-content:center;align-items:center;list-style:none}footer li{display:flex;margin:.2rem}footer a{text-decoration:none;font-size:1.2rem;color:var(--primary-color)}footer:hover{transition:var(--transition);filter:var(--filter)}
:root{--default-color: #fff;--primary-color: #000;--secondary-color: #01d079;--button-color: #149868;--lighting-color: #f7e11c;--linear-gradient: linear-gradient(89.98deg, #256eff 0.02%, #f84f60 102.46%);--alternative-color: #c2c2c2;--transition: all 0.3s linear;--filter: brightness(0.9);--border-radius: 1.5rem;--border-color: #c2c2c24f}.dark-theme{--default-color: #131219;--primary-color: #fff;--lighting-color: #c2c2c2;--secondary-color: #256eff;--linear-gradient: linear-gradient(89.98deg, #256eff 0.02%, #f84f60 102.46%)}.light-theme{--default-color: #FFF;--primary-color: #000;--secondary-color: #01d079;--linear-gradient: linear-gradient(89.98deg, #01d079 0.02%, #fdec59 102.46%)}*,*:after,*:before{box-sizing:border-box;margin:0;padding:0}html{font-size:12pt}body{background:#fff;background:var(--default-color);color:#000;color:var(--primary-color);font-family:"Poppins",sans-serif}@media screen and (max-width: 800px){html{font-size:10pt}h1,h2{font-size:1rem}}@media screen and (max-width: 414px){html{font-size:10pt}h1,h2{font-size:1rem}nav{width:100vw;height:auto}}@media screen and (max-width: 584px){html{font-size:10pt}h1,h2{font-size:1rem}nav{width:100vw;height:auto}}@media screen and (max-width: 280px){.logo{font-size:.7rem}.links{display:flex;flex-direction:column;justify-content:center;align-items:center}.links li{font-size:1rem}nav{width:100vw;height:auto}.nav-center{display:flex;justify-content:space-between;align-items:center}.nav-mode{display:flex;justify-content:center;align-items:center}.main-container{height:auto;width:150%}.main-container .main-content h1{font-size:3rem;margin-top:-2rem}.cases-content{display:flex;flex-direction:column;justify-content:center;align-items:center}.cases-content h1{font-size:3rem}.scan-content{display:flex;flex-direction:column;justify-content:center;align-items:center}.scan-content h2{font-size:3rem}}@media screen and (max-width: 320px){nav{width:100vw;height:auto}html{font-size:10pt}h1,h2{font-size:1rem}}
